.yx-big {
	box-sizing: border-box;
	font-size: 18px;
	text-align: center;
	line-height: 48px;
	color: #1890ff;
}

.animated {
	animation-duration: 1s;
	animation-fill-mode: both;
}

@keyframes fadeInDown {
	from {
		opacity: 0;
		transform: translate3d(0, -100%, 0);
	}

	to {
		opacity: 1;
		transform: translate3d(0, 0, 0);
	}
}

@keyframes fadeOutDown {
	from {
		opacity: 1;
	}

	to {
		opacity: 0;
		transform: translate3d(0, 100%, 0);
	}
}

.fadeInDown {
	animation-name: fadeInDown;
}

.fadeOutDown {
	animation-name: fadeOutDown;
}

.fadeIn-Out {
	animation-name:fadeInDown, fadeOutDown;
	animation-duration:0.5s, 0.5s;
	animation-timing-function:linear, linear;
	animation-delay:0s, 1s;
	animation-fill-mode:forwards, forwards;
	animation-direction:normal, alternate;
}
.flex{
	display: flex;
	align-items: center;
	justify-content: center;
	text-align: center;
}
.num-oneitem {
	height: auto;
	margin: 15px 5px;
	width: 100%;
	justify-content: center;
	align-items: center;
	box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.5);
	background-color: white;
}

.num-container {
	width: auto;
	display: flex;
	flex: 1 1;
	flex-wrap:wrap;
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #e3f2fd;
}

li {
  float: left;
}

li a {
  display: block;
  color: black;
  text-align: center;
  padding: 14px 16px;
  text-decoration: none;
}

/*鼠标移动到选项上修改背景颜色 */
li a:hover {
  background-color: #6489aa;
  color: white;
}
.active {
  background-color: #1890ff;
  color: white;
}
.container-flex {
  height: 200px;
  display: flex;
  flex-direction: row;
  margin: 0 auto;
  justify-content:center;
  flex-wrap: wrap;
}

.wrapper {
  width: 200px;
  margin: 20px auto;
}
.footer{
  text-align: center;
  background-color: #e3f2fd;
}

.btn {
  outline:none;
  font-weight: 800;
  letter-spacing: 7px;
  border-radius: 3px;
  margin-top: 10px;
  text-decoration: none;
  color: #fff;
  background: #1890ff;
  width: 100%;
  height: 40px;
  text-align: center;
  vertical-align: center;
  padding: 10px 0;
}

input {
  width: 100%;
  display: block;
  margin: 10px auto;
  box-sizing: border-box;
  vertical-align: middle;
  border-radius: 3px;
  height: 38px;
  padding: 0 10px;
  border: 1px solid #dbdbdb;
}

input:focus {
  outline: none;
  border: 2px solid #000000;
}

body{
    background-color: #e3f2fd;
}

.cell-wall {
    display: flex;
    justify-content: center;
    width: 100%;
}
/*
  MOBILE

  device: < 480px
*/
@media screen and (max-width: 480px) {
    .cell-membrane {
        flex-basis: 90vw;
    }
}

/*
  DESKTOP

  device: > 480px
*/
@media screen and (min-width: 480px) {
    .cell-membrane {
        flex-basis: 83.125vw;
    }
}

